import uniRate from '@/components/uni-rate/uni-rate.vue'
export default {
	components: {
		uniRate
	},
	data() {
		return {
			id:"",
			type:"",
			flowStatus:10,
			active:false,
			scrollLeft:0,
			showScrollBar:false,
			detail:"",
			contractDetail:"",
			dailyList:[],
			dailyListNum:0,
			returnList:[],
			imgList:[],
			returnListNum:0,
			statuses: [{
				name: '咨询',
				active: false
			},{
				name: '签约',
				active: false
			},{
				name: '尽职调查',
				active: false
			},{
				name: '贷款',
				active: false
			},{
				name: '参拍',
				active: false
			},{
				name: '办理过户',
				active: false
			},{
				name: '腾退',
				active: false
			},{
				name: '交房',
				active: false
			}]
		}
	},
	onLoad(options) {
		let that = this;
		that.id = options.id;
		that.contractId = options.contractId;
		let theId;
		if(options.contractId){
			theId=options.contractId;
		}
		else{
			theId=options.id;
		}
		that.getDetail(theId);
		
	},
	methods: {
		reload(){
			this.getDetail(this.id);
		},
		getDetail(id){
			const that = this;
			let param = {
				id:id
			}
			var interfaceUrl;
			if(that.contractId){
				interfaceUrl=that.$api.housevisitDetailBycontractid;
			}
			else{
				interfaceUrl=that.$api.housevisitDetail;
			}
			interfaceUrl(param).then(res => {
				if (res.success) {
					let result =res.datas;
					let flowStatus = result.flowStatus;
					switch(flowStatus){
						case 10:
						that.statuses[0].active=true;
						break;
						case 20:
						that.statuses[0].active=true;
						that.statuses[1].active=true;
						break;
						case 30:
						that.statuses[0].active=true;
						that.statuses[1].active=true;
						that.statuses[2].active=true;
						break;
						case 40:
						that.statuses[0].active=true;
						that.statuses[1].active=true;
						that.statuses[2].active=true;
						that.statuses[3].active=true;
						that.scrollLeft=60;
						break;
						case 50:
						that.statuses[0].active=true;
						that.statuses[1].active=true;
						that.statuses[2].active=true;
						that.statuses[3].active=true;
						that.statuses[4].active=true;
						that.scrollLeft=121;
						break;
						case 60:
						that.statuses[0].active=true;
						that.statuses[1].active=true;
						that.statuses[2].active=true;
						that.statuses[3].active=true;
						that.statuses[4].active=true;
						that.statuses[5].active=true;
						that.scrollLeft=200;
						break;
						case 70:
						that.statuses[0].active=true;
						that.statuses[1].active=true;
						that.statuses[2].active=true;
						that.statuses[3].active=true;
						that.statuses[4].active=true;
						that.statuses[5].active=true;
						that.statuses[6].active=true;
						that.scrollLeft=200;
						break;
						case 80:
						that.statuses[0].active=true;
						that.statuses[1].active=true;
						that.statuses[2].active=true;
						that.statuses[3].active=true;
						that.statuses[4].active=true;
						that.statuses[5].active=true;
						that.statuses[6].active=true;
						that.statuses[7].active=true;
						that.scrollLeft=250;
						break;
					}
					that.flowStatus = flowStatus;
					if(result.flowLogList[1]){
						result.flowLogList[1].appendixUrlList=result.flowLogList[1].appendixUrls.split(",");
					}
					that.detail=result;
					that.id = result.id;
					that.getDailyList(result.id);
					that.getReturnList(result.id);
					that.getContract(result.id);
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		getDailyList(id){
			const that = this;
			let param = {
				startPage:1,
				pageSize:2,
				visitId: id
			}
			that.$api.housevisitDailyList(param).then(res => {
				if (res.success) {
					let result = res.datas;
					that.dailyListNum = result.total;
					that.dailyList=result.rows;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		getReturnList(id){
			const that = this;
			let param = {
				startPage:1,
				pageSize:2,
				oriVisitId: id
			}
			that.$api.housevisitReturnList(param).then(res => {
				if (res.success) {
					let result = res.datas;
					that.returnListNum = result.total;
					that.returnList=result.rows;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		getContract(id){
			const that = this;
			let param = {
				id: id
			}
			that.$api.housevisitcontract(param).then(res => {
				if (res.success) {
					let result = res.datas;
					that.contractDetail = result;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		gotoOperate(type){
			const that = this;
			var lengthNum = that.detail.flowLogList.length;
			if(type>(lengthNum+1)){
				uni.showToast({
					title:"请先完成前置进度",
					icon:"none"
				})
				return;
			}
			that.$Router.push({
				path:"/pages/my/manageNavs/process/operate",
				query:{
					type:type,
					visitId:that.id
				}
			})
		},
		gotoAllDaily(){
			const that = this;
			this.$Router.push({
				path: "/pages/my/manageNavs/process/allDaily",
				query:{
					id:that.id
				}
			})
		},
		gotoAllReturn(){
			const that = this;
			this.$Router.push({
				path: "/pages/my/manageNavs/process/allReturn",
				query:{
					id:that.id
				}
			})
		},
		gotoHouse(paimaiId,houseSource) {
			this.$Router.push({
				path: "/pages/home/goodsDetail",
				query:{
					paimaiId:paimaiId,
					houseSource:houseSource
				}
			})
		},
		gotoDaily(id){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/journal/detail",
				query:{
					id:id
				}
			})
		},
		gotoContract(id){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/contract/detail",
				query:{
					id:id
				}
			})
		},
		previewImage(url) {
			const that = this;
			that.imgList = [];
			that.imgList.push(url);
			uni.previewImage({
				urls: that.imgList,
				current: that.imgList[0]
			});
		
		},
	}
}
